import React from 'react'
import './Recipe.css'

const Recipe = ({title,ingredients,calories,image})=>{
    return(
        <div class="item">
            <h1>{title}</h1>
            <h3>Calories : {calories}</h3>
            <ul>
                {
                    ingredients.map(ingredient=>{
                         return <li>{ingredient.text}</li>
                    })
                }
            </ul>
            <img src={image} />
        </div>
    )
}
export default Recipe;